const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			options: {},
			userInfo: {},
			index: '0',
			payType: 'wechat'
		};
	},
	onLoad(options) {
		this.userInfo = app.globalData.userInfo
		// console.log(this.userInfo)
		// console.log(options)
		this.options = options
	},
	methods: {
		radioChange(e) {
			console.log(e);
			this.payType = e.detail.value;
		},
		wxAppletPay() {
			// 微信小程序支付
			uni.showLoading({
				title: '支付信息加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/bay-vip`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					member_id: this.options.id,
					app_type: 'miniapp',
					pay_type: 'wechat'
				},
				success: res => {
					// console.log(res);
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.statusCode == 200) {
						this.runRecharge(res.data)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		runRecharge(payNode) {
			uni.showToast({
				title: "支付加载中...",
				icon: 'loading',
				duration: 1000000
			});
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: payNode.timeStamp,
				nonceStr: payNode.nonceStr,
				package: payNode.package,
				signType: payNode.signType,
				paySign: payNode.paySign,
				success: res => {
					uni.hideToast()
					res = helper.null2str(res)
					// console.log(res)
					// console.log('success:' + JSON.stringify(res));
					uni.showToast({
						title: "支付成功",
						icon: 'success',
						duration: 2000
					});
					// 两秒后返回上一页
					setTimeout(e => {
						uni.reLaunch({
							url:'/pages/paySuccess'
						})
					}, 2000)
				},
				fail: err => {
					uni.showToast({
						title: "支付失败",
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		iAgree() {
			console.log(this.payType);
			uni.showToast({
				title: this.payType
			})
			uni.request({
				url: `${helper.requestUrl}/bay-vip`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					member_id: this.options.id,
					app_type: 'app',
					pay_type: this.payType
				},
				success: res => {
					console.log(res);
					console.log(res.data)
					// 调起支付
					uni.requestPayment({
						provider: 'wxpay',
						orderInfo: res.data, //订单数据
						success: function(res) {
							console.log('success:' + JSON.stringify(res));
							console.log('*********')
						},
						fail: function(err) {
							// console.log('fail:' + JSON.stringify(err));
						}
					});
				}
			});
		}
	}
};
